import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import Router from 'next/router';
import gql from 'graphql-tag';
import Form from '../styled/Form';
import Error from '../ErrorMessage';
// import { ALL_ITEMS_QUERY } from './Items';

const CREATE_ITEM_MUTATION = gql`
    mutation CREATE_ITEM_MUTATION(
        $title: String!
        $description: String!
        $image: String
        $largeImage: String
        $tags: [String!]!
    ) {
        createPost(
            title: $title
            description: $description
            image: $image
            largeImage: $largeImage
            tags: $tags
        ) {
            id
        }
    }
`;

class CreateItem extends Component {

    state = {
        title: '',
        description: '',
        image: '',
        largeImage: '',
        content: '',
        tags: []
    }

    handleChange = (e) => {
        const { name, type, value } = e.target;
        const val = type === 'number' ? parseFloat(value) : value;
        // if(name === 'tags') {
        //     this.setState({tags: [...this.state.tags, ...value.split('#')]});
        //     return
        // }

        this.setState({ [name]: val })
    }
    uploadFile = async e => {

        console.log(e.target.files);


        let files = e.target.files;

        if (e.target.files.length > 1) {

            for (let i = 0; i < e.target.files.length; i++) {
                files = files;
                async (files) => {
                    console.log('Uploading file #' + i);
                    // const files = e.target.files;
                    const data = new FormData();
                    data.append('file', files[i]);
                    data.append('upload_preset', 'kpblog');

                    const res = await fetch
                        ('https://api.cloudinary.com/v1_1/kevinpena/image/upload', {
                            method: 'POST',
                            body: data
                        });

                    const file = await res.json();
                    this.setState({
                        image: file.secure_url,
                        largeImage: file.eager[0].secure_url
                    })
                }
            }
        }
        else {
            console.log('Uploading');
            console.log(e);
            const files = e.target.files;
            const data = new FormData();
            data.append('file', files[0]);
            data.append('upload_preset', 'kpblog');

            const res = await fetch
                ('https://api.cloudinary.com/v1_1/kevinpena/image/upload', {
                    method: 'POST',
                    body: data
                });

            const file = await res.json();
            this.setState({
                image: file.secure_url,
                largeImage: file.eager[0].secure_url
            })
        }


    }

    render() {
        return (
            <Mutation mutation={CREATE_ITEM_MUTATION}>
                {(createPost, { loading, error }) => (
                    <Form onSubmit={async e => {
                        e.preventDefault();
                        let words = await this.state.tags.split('#');
                        await words.shift();
                        words = await words.slice();
                        words = await words.map(word => word.toLowerCase());
                        words = await words.map(word => word.trim());
                        const res = await createPost({
                            variables: {
                                title: this.state.title,
                                description: this.state.description,
                                image: this.state.image,
                                largeImage: this.state.largeImage,
                                content: this.state.content,
                                tags: words
                            }
                        });
                        Router.push({
                            pathname: '/post',
                            query: { id: res.data.createPost.id }
                        })
                    }}>
                        <Error error={error} />
                        <fieldset disabled={loading} aria-busy={loading}>
                            <label htmlFor="File">
                                Image
                    <input
                                    type="file"
                                    id="file"
                                    name="file"
                                    multiple
                                    placeholder="Upload an image"
                                    // value={this.state.image}
                                    onChange={this.uploadFile}
                                    required
                                />
                                {this.state.image && <img style={{ width: '200px' }} src={this.state.image} alt="Upload Preview" />}
                            </label>

                            <label htmlFor="title">
                                Title
                    <input
                                    type="text"
                                    id="title"
                                    name="title"
                                    placeholder="Title"
                                    value={this.state.title}
                                    onChange={this.handleChange}
                                    required />
                            </label>
                            <label htmlFor="description">
                                Description
                    <textarea
                                    id="description"
                                    name="description"
                                    placeholder="Enter a description"
                                    value={this.state.description}
                                    onChange={this.handleChange}
                                    required />
                            </label>
                            <label htmlFor="content">
                                Content
                    <textarea
                                    id="content"
                                    name="content"
                                    placeholder="Content"
                                    value={this.state.content}
                                    onChange={this.handleChange}
                                />
                            </label>
                            <label htmlFor="tags">
                                Tags
                    <input
                                    type="text"
                                    id="tags"
                                    name="tags"
                                    placeholder="Tag"
                                    value={this.state.tags}
                                    onChange={this.handleChange}
                                />
                            </label>
                            <button type="submit">Submit</button>
                        </fieldset>
                    </Form>
                )}
            </Mutation>

        )
    }
}

export default CreateItem;
export { CREATE_ITEM_MUTATION };
